import React from 'react';
import { useTranslation } from 'react-i18next';
import { Container, Typography, Box, List, ListItem, ListItemText, Divider, Paper } from '@mui/material';

const ExperiencePage = () => {
  const { t } = useTranslation('career');
  
  const companies = t('companies', { returnObjects: true });

  return (
    <Container className="p-6 max-w-4xl mx-auto" maxWidth="md">
      <Paper elevation={1} className="mb-20 p-4" style={{ backgroundColor: '#fafafa', padding: '2rem', borderRadius: '8px' }}>
        <Typography variant="body1" style={{ lineHeight: '1.8', color: '#555', fontWeight: 'normal', textAlign: 'left', fontStyle: 'italic' }}>
          “{t('summary')}”
        </Typography>
      </Paper>
      {companies.map((company, companyIndex) => (
        <div key={companyIndex} className="mb-8">
          <Box display="flex" alignItems="baseline" className="mb-2">
            <Typography variant="h5" className="text-left" style={{ marginRight: '1rem', fontWeight: 'bold', color: '#333' }}>
              {company.name}
            </Typography>
            <Typography variant="subtitle2" className="text-left" style={{ color: '#777' }}>
              ({company.duration})
            </Typography>
          </Box>
          <List>
            {company.projects.map((project, projectIndex) => (
              <Box key={projectIndex}>
                <Paper elevation={1} className="mb-4" style={{ borderLeft: '4px solid #1976d2' }}>
                  <ListItem className="p-4 rounded-lg">
                    <ListItemText
                      primary={<Typography variant="h6" style={{ color: '#1976d2' }}>{project.title}</Typography>}
                      secondary={
                        <>
                          <Typography variant="subtitle2" style={{ color: '#777' }}>{project.duration}</Typography>
                          <Typography variant="body2" style={{ marginTop: '0.5rem', color: '#555' }}>{project.description}</Typography>
                        </>
                      }
                    />
                  </ListItem>
                </Paper>
                {projectIndex < company.projects.length - 1 && <Divider />}
              </Box>
            ))}
          </List>
        </div>
      ))}
    </Container>
  );
};

export default ExperiencePage;
